@import "../../common/styles/sass/variables";
@import "../../common/styles/sass/mixins";

.project {
  width: 450px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $bgSecondColor;

  .imgContainer {
    width: 100%;
    height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      background-color: black;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.4;
    }

    .button {
      position: relative;
      text-decoration: none;
      background-color: whitesmoke;
      padding: 10px 20px;
      z-index: 1;
      color: dodgerblue;
    }
  }

  .projectInfo {
    padding: 25px 20px;

    .projectTitle {
      //font-family: 'Josefin Sans', sans-serif;
      //font-size: 16px;
      //line-height: 1.2;
      //color: $textColor;
      @include text(('Josefin Sans', sans-serif),16px,1.2);
      letter-spacing: 1px;
      margin: 0 0 5px;
    }

    .description {
      //font-family: 'Poppins', sans-serif;
      //font-size: 12px;
      //line-height: 1.8;
      //color: $textColor;
      @include  text(('Poppins', sans-serif),12px,1.8);
      font-weight: 400;
    }
  }
}



