@import "../common/styles/sass/variables";

.nav {
  border: 1px solid red;
  display: flex;
  justify-content: space-between;
  & a {
    font-weight: 400;
    text-decoration: none;
    color: $textColor;
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    margin: 5px 20px;
    line-height: 1.5;
  }
}

